.page3-text-box {
    width: 46%;
    text-align: start;
    margin: auto;
}
.page3-content {
    margin: 37px 0 0 14px;
    display: flex;
    gap: 4px;
}
.page3-bold-text {
    font-size: 34px;
    line-height: 33px;
}
b.bold1 {
    color: yellowgreen;
    margin: 0;
    padding: 0;
}
.logo-text-page3 {
    height: 40px;
    width: 40px;
    display: block;
    background: white;
    border-radius: 50%;
    box-shadow: inset 0 0 2px #857a7aad, 0 2px 10px rgb(0 0 0 / 26%);
    line-height: 53px;
    text-align: center;
    color: darkblue;
}
.page3-content-text {
    width: 18rem;
    font-size: 16px;
    opacity: 0.8;
}


@media (min-width:200px) and (max-width:576px){
    .page3-text-box {
        width: 100%;
        margin:14px 0 50px;
    }.page3-bold-text {
        font-size: 25px;
        
    }.page3-content {
        margin: 20px 0 0 20px;
    }
}
@media (min-width:576px) and (max-width:900px){
    .page3-text-box {
        width: 100%;
        margin:14px 0 50px;
    }.page3-bold-text {
        text-align: center;
        font-size: 25px;
    }.page3-content {
        margin: 20px 0 0 20px;
    }
}